class Character {
    constructor(pos, type) {
        this.pos = pos;
        this.type = type;
        this.right = 0;
        this.left = 0;
        this.up = 0;
        this.down = 0;
    }

    drawChar(ctx) {
        debugger
        ctx.beginPath();
        ctx.rect(this.pos[0] + this.right - this.left,
                 this.pos[1] + this.down - this.up,
                 this.pos[2],
                 this.pos[3]);
        ctx.fillStyle = this.type === "user" ? "#0000FF" : "#FF0000";
        ctx.fill();
        ctx.closePath();
    }

    draw(ctx) {
        debugger
        ctx.clearRect(0, 0, 700, 600);
        this.drawChar(ctx);
    }

    setKeyListeners() {
        document.addEventListener("keydown", keyDown);
        document.addEventListener("keyup", keyUp);
    }

    keyDown (e) {
        if (e.key == "Right" || e.key == "ArrowRight") {
            this.right = 10;
            this.draw(ctx);
        }
        else if (e.key == "Left" || e.key == "ArrowLeft") {
            this.left = 10;
            this.draw(ctx);
        }
        else if (e.key == "Up" || e.key == "ArrowUp") {
            this.up = 10;
            this.draw(ctx);
        }
        else if (e.key == "Down" || e.key == "ArrowDown") {
            this.down = 10;
            this.draw(ctx);
        }
    }

    keyUp (e) {
        if (e.key == "Right" || e.key == "ArrowRight") {
            this.right = 0;
        }
        else if (e.key == "Left" || e.key == "ArrowLeft") {
            this.left = 0;
        }
        else if (e.key == "Up" || e.key == "ArrowUp") {
            this.up = 0;
        }
        else if (e.key == "Down" || e.key == "ArrowDown") {
            this.down = 0;
        }
    }
}

export default Character;